<template>
  <div class="playlist">
    <!-- <div class="go-back" >
      <span @click="$router.go(-1)">⬅</span>
    </div>   -->
   <!-- <span>{{$route.query.id}}</span> -->
    <Introduce v-if="playlist" :dd="playlist"></Introduce>
    <h3>歌曲列表</h3>
    <ul class="songlist">
      <!-- 如果这个playlist存在 -->
      <template v-if="playlist">
        <ol>
          <SongListItem
            v-for="(item, index) in tracks"
            :key="index"
            :song-item="item" 
            :index="index"
            :tracks="tracks"
            @translate-currentid="$emit('translate-currentid',$event)"
          />
        </ol>
      </template>
    </ul>
  </div>
</template>

<script>
import Introduce from "@/components/Introduce.vue";
import SongListItem from "@/components/SongListItem.vue";
export default {
  components: {
    SongListItem,
    Introduce
  },
  data() {
    return {
      playlist: null,
      tracks:[]
    };
  },
  methods: {
    getPlayListDetail(id) {
      //推荐歌单详情
      this.axios
        .get("https://music.kele8.cn/playlist/detail", {
          params: {
            id: id
          }
        })
        .then(reponse => {
          
          ({ playlist: this.playlist } = reponse.data); 
          this.tracks = reponse.data.playlist.tracks;
          // window.console.log(this.$route)
          //  window.console.log(reponse.data.playlist)
          //  window.console.log(this.tracks)
        });
    }
  },	
  created() {
    // window.console.log("xxx");
  },
  beforeRouteEnter(to, from, next) {
    // 路由进入 当前组件实例化之前就 拿数据
    // window.console.log(to)
    next(vm => {
      vm.getPlayListDetail(to.query.id);
    });
  }
};
</script>

<style lang='less'>
.playlist {
  .go-back {
    position: absolute;
    top: 1%;
    right: 8%;
    span{
      font-size: 38px;
      color: rgb(225, 229, 238);
    }
  }
  h3 {
    background-color: #eeeff0;
    padding: 5px;
    font-size: 14px;
    color: #666;
  }
}
</style>
