<template>
  <div class="artist">
    <div class="artist-bg">
      <span class="lessThan" @click="$router.go(-1)">＜</span>
      <img :src="this.$route.query.img" alt />
      <div class="artist-info">
        <p>歌手：{{this.$route.query.name}}</p>
      </div>
    </div>
    <h3>歌手详情</h3>
    <div class="info">
      <p class="words" :class="{active:isActive}">{{this.info}}</p>
      <div class="full" @click="isActive = !isActive">
        完整歌手介绍
        <template v-if="isActive">
          <span class="fa fa-chevron-down"></span>
        </template>
        <template v-else>
          <span class="fa fa-chevron-up"></span>
        </template>
      </div>
    </div>
    <h3>热门单曲</h3>
    <SongListItem v-for="(item, index) in songs" :song-item=item :index = index :key="index" :songs="songs" @translate-currentid="$emit('translate-currentid',$event)"/>
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
export default {
  data() {
    return {
      isActive: true,
      info: "",
      songs:[]
    };
  },
  components:{
    SongListItem
  },
  methods: {
    getArtistDesc() {
      this.axios
        .get("https://music.kele8.cn/artist/desc?id=" + this.$route.query.id)
        .then(response => {
          this.info = response.data.briefDesc;
          // window.console.log(this.info);
        });
    },
    getArtistSongs(){
      this.axios.get("https://music.kele8.cn/artists?id=" + this.$route.query.id).then(response =>{
        this.songs = response.data.hotSongs.slice(0, 10)
        window.console.log(this.songs)
      })
    }
  },
  created() {
    this.getArtistDesc();
    this.getArtistSongs()
  }
};
</script>

<style lang="less">
div.artist {
  background-color: #fcfcfd;
  div.artist-bg {
    position: relative;
    span.lessThan {
      position: absolute;
      left: 1%;
      top: 1%;
      font-size: 200%;
      color: white;
    }
    // img {
    // }
    div.artist-info {
      position: absolute;
      left: 4%;
      bottom: 10%;
      p {
        color: white;
      }
    }
  }
  h3 {
    padding-left: 4%;
    padding-top: 3%;
    font-weight: 600;
    font-size: 110%;
  }
  div.info {
    color: #666;
    padding: 0 20px;
    p.words {
      margin-top: 3%;
      &.active {
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }
    div.full {
      margin: 5% 0 5%;
      text-align: center;
      span {
        color: #888;
      }
    }
  }
}
</style>