<template>
  <div class="introduce">
    <div class="plhead">
      <div class="plhead-bg" :style="{'background-image':'url(' + dd.coverImgUrl +' )'}"></div>
      <div class="go-back">
        <span @click="$router.go(-1)">⬅</span>
      </div>
      <div class="plhead-wrap">
        <div class="fl">
          <img :src="dd.coverImgUrl" alt />
          <span>歌单</span>
          <i class="fa fa-headphones">{{translatePlayCount(dd.playCount)}}</i>
        </div>
        <div class="fr">
          <h2>{{dd.name}}</h2>
          <div class="icon" :style="{'background-image':'url(' + dd.creator.avatarUrl +' )'}"></div>
        </div>
      </div>
    </div>
    <div class="intro">
      <div class="intrp-tags">
        标签:
        <em v-for="(item, index) in dd.tags" :key="index">{{item}}</em>
      </div>
      <div class="u-intro">
        <div class="jian" :class="{active:isActive}">
          <span>简介:</span>
          <span>
            <i>{{dd.description}}</i>
            <br />
          </span>
        </div>
        <div class="okc" v-show="ok">
          <div class="arrows" @click="change">
            <template v-if="isActive">
              <span class="fa fa-chevron-down"></span>
            </template>
            <template v-else>
              <span class="fa fa-chevron-up"></span>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["dd"],
  data() {
    return {
      isActive: true,
      ok: true
    };
  },
  methods: {
    nick() {
      let boxNum = this.$el.children[1].children[1].children[0].offsetHeight;
      if (boxNum <= 21) {
        this.ok = true;
      }
    },
    change: function() {
      this.isActive = !this.isActive;
    },
    translatePlayCount(n) {
      if (n > 99999999) {
        return (n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        return (n / 10000).toFixed(1) + "万";
      }
    }
  },
  mounted() {
    this.nick();
  }
};
</script>

<style lang='less'>
.introduce {
  .plhead {
    position: relative;
    padding: 16% 10px 30px 15px;
    overflow: hidden;
    .plhead-bg {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: 50%;
      transform: scale(1.5);
      filter: blur(20px);
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background-size: cover;
    }
    .plhead-wrap {
      display: flex;
      .fl {
        width: 126px;
        height: 126px;
        position: relative;
        overflow: hidden;
        img {
          width: 100%;
          vertical-align: middle;
        }
        span {
          color: white;
          font-size: 10px;
          position: absolute;
          top: 15px;
          left: 0;
          padding: 2px 8px;
          background: rgba(217, 48, 48, 0.8);
          border-radius: 0 10px 10px 0;
        }
        i {
          font-style: normal;
          font-size: 10px;
          position: absolute;
          top: 3px;
          right: 5px;
          color: white;
        }
      }
      .fr {
        padding: 10px 15px;
        flex: 1 1 auto;
        position: relative;
        h2 {
          color: white;
          font-size: 17px;
          padding: 10px 10px 0px 10px;
          position: absolute;
          top: 0;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        div.icon {
          border-radius: 50%;
          width: 30px;
          height: 30px;
          position: absolute;
          top: 20vw;
          left: 25px;
          background-size: cover;
          background-position: top;
        }
      }
    }
  }
  .intro {
    color: gray;
    padding: 10px 15px;
    .intrp-tags {
      margin-bottom: 10px;
      em {
        font-size: 12px;
        font-style: normal;
        margin: 8px;
      }
    }
    .u-intro {
      div.jian {
        &.active {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
        span {
          //   position: relative;
          white-space: pre-wrap;
          i {
            // position: relative;
            // top: -10px;
            font-style: normal;
          }
        }
      }
      div.okc {
        text-align: right;
        &.active {
          display: none;
        }
        div.arrows {
          display: inline-block;
          width: 14px;
          height: 14px;

          span {
            font-size: 14px;
          }
        }
      }
    }
  }
}
</style>